<template>
    <section>
        <div class='entry' data-v-faheader>
            <form data-v-faheader>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>年</p>
                    <input type="number" name="year" v-model="header.year" data-v-faheader>
                </div>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>月</p>
                    <input type="number" name="month" v-model="header.month" data-v-faheader>
                </div>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>日</p>
                    <input type="number" name="day" v-model="header.day" data-v-faheader>
                </div>
                <div class='entryInputBlock' data-v-faheader>
                    <p data-v-faheader>口座ID</p>
                    <select name="bank" v-model="header.bank" v-on:change="changeBank" style="width:120px;">
                        <!-- <option value=""></option> -->
                        <option v-for='(bank, index) in bankId' :key='index' v-bind:value="bank.id">
                            {{bank.name}}
                        </option>
                    </select>
                </div>
            </form>
        </div>
    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

export default {
    name : 'FAheader',

    // URL情報
    mixins : [apiUrls],
    // FAinputコンポーネントとの通信用オブジェクト
    props : ["header"],

    data(){
        return{
            // bankマスタ
            bankId:[
                    {
                        id      : "",
                        name    : "",
                        balance : 0,
                        year    : 0,
                        period  : 0,
                        default : false
                    }
                ]
        }
    },

    //--------------------------------------
    // 初期化処理
    //--------------------------------------
    created(){
        // トークン取得
        let token = Cookies.get('token')
        if(token == null)
        {
            return
        }

        // thisを格納
        const self = this

        // Bank取得API call
        axios.get(this.apiUrls["getBanks"],
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                return
            }
            
            // 取得成功時
            for(let i=0; i<response.data.data.length; i++)
            {
                // テンプレート用オブジェクトをコピー
                let data = Object.assign({}, self.bankId[0])
                
                // 値設定
                data.id      = response.data.data[i].id
                data.name    = response.data.data[i].name
                data.balance = response.data.data[i].balance
                data.year    = response.data.data[i].currentYear
                data.period  = response.data.data[i].currentPeriod
                data.default = response.data.data[i].default
                
                // 明細オブジェクト追加
                self.bankId.push(data)

                // デフォルト口座の場合
                if(data.default)
                {
                    self.header.bank    = data.id
                    self.header.balance = data.balance
                }
            }
        })
        .catch(function(error){
            return
        })
    },

    //--------------------------------------
    // Methods
    //--------------------------------------
    methods:{
        //--------------------------------------
        // Bank切替イベントハンドラ
        //--------------------------------------
        changeBank(){
            // 残高の再設定
            for(let i=0; i<this.bankId.length; i++)
            {
                if(this.bankId[i].id == this.header.bank)
                {
                    this.header.balance = this.bankId[i].balance
                    return
                }
            }
        }
    }
}
</script>

<style scoped>
    .entry[data-v-faheader]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .entryInputBlock[data-v-faheader]{
        float:left;
        padding:5px;
    }

    input[data-v-faheader]{
        width:80px;
    }
    input.bankInput[data-v-faheader]{
        width:120px;
    }

    p[data-v-faheader]{
        margin-top:10px;
        margin-bottom: 5px;
    }
</style>





