<template>
    <transition name="faentry">
        <div class='entry' data-v-faentry>
            <form data-v-faentry>
                <div class='entryInputBlock' data-v-faentry>
                    <p data-v-faentry>科目</p>
                    <select name="accInput" v-model="entry.accId" v-on:change="selectAccount">
                        <!-- <option value=""></option> -->
                        <option v-for='(account, index) in accId' :key='index' v-bind:value="account.id">{{account.id}}：{{account.name}}</option>
                    </select>
                </div>
                <div class='entryInputBlock' data-v-faentry>
                    <p data-v-faentry>内容・内訳</p>
                    <input type="text" name="entryTxtInput" v-model="entry.entryTxt">
                </div>
                <div class='entryInputBlock' data-v-faentry>
                    <p data-v-faentry>金額</p>
                    <input type="number" name="valueInput" v-model="entry.value">
                </div>
                <div class='entryInputBlock' data-v-faentry>
                    <p data-v-faentry>値引額</p>
                    <input type="number" name="discountInput" v-model="entry.discount">
                </div>
                <div class='entryInputBlock' data-v-faentry>
                    <p data-v-faentry>税・手数料等</p>
                    <input type="number" name="extInput" v-model="entry.ext">
                </div>
                <div class='entryInputBlock' data-v-faentry>
                    <p data-v-faentry>備考</p>
                    <input type="text" name="remarksInput" v-model="entry.remarks">
                </div>
            </form>

            <div class='entryTotal' data-v-faentry>
                <p data-v-faentry>明細小計：{{ entryTotal}}</p>
                <div class="buttonBase faentryButton" data-v-faentry v-on:click = "delEntry">明細削除</div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name : 'FAentry',
    
    // FAinput通信用オブジェクト
    props: ['index', 'entry', 'accId'],

    data(){
        return{

        }
    },

    // -----------------------------------------
    //  Methods
    // -----------------------------------------
    methods: {
        // -----------------------------------------
        //  科目選択時
        // -----------------------------------------
        selectAccount(){
            // 科目検索 符号決定
            for(var i=0; i<this.accId.length; i++)
            {
                if(this.accId[i].id == this.entry.accId)
                {
                    // 費用科目の場合
                    if(this.accId[i].type == 1)
                    {
                        this.entry.entryAgg = -1
                        return
                    }
                    // 収益科目の場合
                    this.entry.entryAgg = 1
                    return
                }
            }
        },

        // -----------------------------------------
        //  明細削除時
        // -----------------------------------------
        delEntry : function(){
            this.$emit('entry-del-event', this.index)
        }
    },

    computed : {
        // -----------------------------------------
        //  明細小計計算
        // -----------------------------------------
        entryTotal: function(){
            let total = Number(this.entry.value) - Number(this.entry.discount) + Number(this.entry.ext)
            let retObj = {
                index : 0,
                total : 0
            }
            retObj.index = this.index
            retObj.total = total * this.entry.entryAgg

            this.$emit('entry-input-event', retObj)
            return total
        }
    }
}
</script>

<style scoped>
    .entry[data-v-faentry]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .entryInputBlock[data-v-faentry]{
        float:left;
        padding:5px;
    }

    .entryTotal[data-v-faentry]{
        clear:left;
        padding:5px;
    }

    p[data-v-faentry]{
        margin-top:10px;
        margin-bottom: 5px;
    }

    .faentryButton[data-v-faentry]{
        margin : 15px 0px;
    }

    .faentry-enter-active, .faentry-leave-active{
        transition: opacity 0.3s;
    }
    .faentry-enter{
        opacity: 0;
        /* transform : translatex(-250px); */
    }
    .faentry-leave-to{
        opacity: 0;
        /* transform : translatex(-250px); */
    }
</style>





