<template>
    <section data-v-fainput>
        <!-- 画面ヘッダ -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 

        <!-- 家計簿入力ヘッダ -->
        <FAheader 
            v-bind:header = 'faHeader'
        />

        <!-- 最新情報に更新ボタン -->
        <div class='buttonBase fainputButton' data-v-fainput>最新情報に更新</div>

        <!-- 家計簿入力明細 -->
        <FAentry
            v-for='(entry, index) in faEntrys' :key='index'
            v-if='index > 0'
            v-bind:index = 'index'
            v-bind:entry = 'entry'
            v-bind:accId = 'accId'
            v-on:entry-input-event = 'entryUpdate'
            v-on:entry-del-event = 'delEntry'
        />

        <!-- 明細追加ボタン -->
        <div class='buttonBase fainputButton' data-v-fainput v-on:click = 'addEntry'>明細追加</div>

        <!-- ヘッダ入力部 -->
        <section>
            <div class='entry' data-v-fainput>
                <form data-v-fainput>
                    <div class='entryInputBlock' data-v-fainput>
                        <p class='formLabel' data-v-fainput>外税・手数料等</p>
                        <input type='number' name='hExtInput' v-model="faHvalue.hExt">
                    </div>
                    <div class='entryInputBlock' data-v-fainput>
                        <p class='formLabel' data-v-fainput>値引額</p>
                        <input type='number' name='hDiscountInput' v-model="faHvalue.hDiscount">
                    </div>
                    <div class='entryInputBlock' data-v-fainput>
                        <p class='formLabel' data-v-fainput>コメント</p>
                        <input type='text' name='hCommentInput' class='commentInput' v-model="faHvalue.hComment" data-v-fainput>
                    </div>
                </form>
            </div>
        </section>

        <!-- 伝票金額 -->
        <section>
            <div class='entry' data-v-fainput>
                <table  data-v-fainput>
                    <tr><td>伝票合計金額：</td><td>{{ total }}</td></tr>
                    <tr><td>現残高：</td><td>{{ faHeader.balance }}</td></tr>
                    <tr><td>登録後残高：</td><td>{{ afterBalance }}</td></tr>
                </table>
            </div>
        </section>

        <!-- 登録ボタン -->
        <div class='buttonBase fainputButton' v-on:click = "registor" data-v-fainput>登録</div>

        <!-- 登録確認用モーダル -->
        <Modal
            v-show="confirmModal"
            v-bind:mtypeIn='1'
            v-on:modal-yes-event    = "confirmYes"
            v-on:modal-no-event     = "confirmNo"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <table  data-v-fainput>
                    <p>伝票を登録しますか？</p>
                    <tr><td>伝票合計金額：</td><td>{{ total }}</td></tr>
                    <tr><td>現残高：</td><td>{{ faHeader.balance }}</td></tr>
                    <tr><td>登録後残高：</td><td>{{ afterBalance }}</td></tr>
                </table>
            </template>
        </Modal>

        <!-- 登録完了モーダル -->
        <Modal
            v-show="completeModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                家計簿に登録しました。
            </template>
        </Modal>

        <!-- 登録失敗モーダル -->
        <Modal
            v-show="errorModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postErrorModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>家計簿に登録に失敗しました。</p>
                <p>(コード：{{errorCode}})</p>
            </template>
        </Modal>

        <Loading v-show="loading" />

    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

import ViewHeader   from '../components/ViewHeader'
import FAheader     from '../components/FAheader'
import FAentry      from '../components/FAentry'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'

export default {
    name : 'FAinput',
    mixins : [apiUrls],
    
    // ------------------------------------
    //  使用コンポーネント一覧
    // ------------------------------------
    components : {
        ViewHeader,
        FAentry,
        FAheader,
        Modal,
        Loading
    },

    // ------------------------------------
    //  初期化処理
    // ------------------------------------
    created : function(){

        // FAヘッダに現在日付を設定
        let dt = new Date()
        this.faHeader.year  = dt.getFullYear()
        this.faHeader.month = dt.getMonth()+1
        this.faHeader.day   = dt.getDate()

        //  科目取得処理
        // トークン取得
        let token = Cookies.get('token')
        if(token == null)
        {
            return
        }

        // thisを格納
        const self = this

        // Bank取得API call
        axios.get(this.apiUrls["getAccounts"],
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                return
            }
            
            // 取得成功時
            for(let i=0; i<response.data.data.length; i++)
            {
                // テンプレート用オブジェクトをコピー
                let data = Object.assign({}, self.accId[0])
                
                // 値設定
                data.id     = response.data.data[i].accid
                data.name   = response.data.data[i].name
                data.type   = response.data.data[i].accType
                // 明細オブジェクト追加
                self.accId.push(data)
            }

            // テンプレート用オブジェクト削除
            self.accId.shift()
            console.log(JSON.stringify(self.accId))
        })
        .catch(function(error){
            return
        })
    },

    data(){
        return {
            moduleName : 'FA',
            viewName : '家計簿入力',

            // ------------------------------------
            // FAヘッダ通信用オブジェクト
            // ------------------------------------
            faHeader: {
                year : null,
                month : null,
                day : null,
                bank : '',
                balance : 10
            },

            // ------------------------------------
            // FA明細通信用オブジェクト
            // ------------------------------------
            faEntrys: [
                {
                    accId: '',
                    entryTxt: '',
                    value: null,
                    discount: null,
                    ext: null,
                    remarks: '',
                    entrySum: null,
                    entryAgg : null
                },
                {
                    accId: '',
                    entryTxt: '',
                    value: null,
                    discount: null,
                    ext: null,
                    remarks: '',
                    entrySum: null,
                    entryAgg : null
                }
            ],

            // ------------------------------------
            //  ヘッダ入力部オブジェクト
            // ------------------------------------
            faHvalue : {
                hExt : null,
                hDiscount : null,
                hComment : ''
            },

            // ------------------------------------
            // 科目マスタ
            // ------------------------------------
            accId : [
                {
                    id      : "",
                    name    : "" ,
                    type    : null
                }
            ],

            //  確認モーダル表示制御
            confirmModal : false,

            completeModal : false,
            errorModal    : false,

            loading : false,

            errorCode : ''
        }
    },

    computed : {
        // ------------------------------------
        // 伝票合計金額算出
        // ------------------------------------
        total(){
            let total = 0

            //  明細集計
            for (var i = 1; i < this.faEntrys.length; i++) {
                total += this.faEntrys[i].entrySum;
            }

            //  ヘッダレベル
            total = total - Number(this.faHvalue.hExt) + Number(this.faHvalue.hDiscount)

            return total
        },

        // ------------------------------------
        //  登録後残高算出
        // ------------------------------------
        afterBalance(){
            return this.faHeader.balance + this.total
        }
    },

    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:{
        // ------------------------------------
        // 明細更新時
        // ------------------------------------
        entryUpdate: function (retObj) {
            // 明細合計金額を設定
            this.faEntrys[retObj.index].entrySum = retObj.total
        },

        // ------------------------------------
        // 明細追加
        // ------------------------------------
        addEntry : function(){
            // テンプレート用オブジェクトをコピー
            let faEntry = Object.assign({}, this.faEntrys[0])
            // 明細オブジェクト追加
            this.faEntrys.push(faEntry)
        },

        // 明細削除
        delEntry : function(index){
            this.$delete(this.faEntrys, index)
        },

        // ------------------------------------
        // 登録ボタン押下
        // ------------------------------------
        registor : function()
        {
            // this.callPostFaData()

            // 確認用モーダル表示
            this.confirmModal = true
        },

        // ------------------------------------
        // 登録確認モーダル Yes押下
        // ------------------------------------
        confirmYes (){
            // モーダル閉じる
            this.confirmModal = false
            // loading
            this.loading = true

            // 家計簿登録API呼出
            this.callPostFaData()
        },

        // ------------------------------------
        // 登録確認モーダル No押下
        // ------------------------------------
        confirmNo (){
            // モーダル閉じる
            this.confirmModal = false
        },

        // ------------------------------------
        // 家計簿登録API呼出
        // ------------------------------------
        callPostFaData(){
            let res = {
                    header : {
                        year    : Number(this.faHeader.year),
                        month   : Number(this.faHeader.month),
                        day     : Number(this.faHeader.day),
                        bank    : this.faHeader.bank,
                        hExt    : Number(this.faHvalue.hExt),
                        hDiscount: Number(this.faHvalue.hDiscount),
                        hComment: this.faHvalue.hComment,
                        hTotal  : this.total
                    },
                    entries : this.faEntrys
                }
            console.log(JSON.stringify(res))


            const self   = this

            // トークン取得
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }

            // ログインAPI call
            axios.post(this.apiUrls["postFaData"], 
                {
                    header : {
                        year    : Number(this.faHeader.year),
                        month   : Number(this.faHeader.month),
                        day     : Number(this.faHeader.day),
                        bank    : this.faHeader.bank,
                        hExt    : Number(this.faHvalue.hExt),
                        hDiscount: Number(this.faHvalue.hDiscount),
                        hComment: this.faHvalue.hComment,
                        hTotal  : this.total
                    },
                    entries : this.faEntrys
                },
                {
                    headers : {
                        'Authorization' : token
                    }
                }
            )
            .then(function(response){
                self.loading    = false
                // HTTP call success
                if(response.status == 200)
                {
                    // API success
                    if(response.data.status == 'S')
                    {
                        self.completeModal = true
                        return
                    }
                    // API error
                    self.errorCode  = response.data.code
                    self.errorModal = true
                    return
                }
                // HTTP error
                self.errorModal = true
            })
            .catch(function(error){
                self.loading    = false
                self.errorModal = true
            })
        },

        // ------------------------------------
        // 家計簿登録後モーダルOK
        // ------------------------------------
        postModal(){
            this.completeModal = false
            location.reload()
            window.scrollTo(0,0)
            return
        },
        postErrorModal(){
            this.errorModal = false
            return
        }
    }
}
</script>

<style scoped>
    .fainputButton[data-v-fainput]{
        /* width:100px; */
        margin : 30px 15px;
    }

    .entry[data-v-fainput]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .entryInputBlock[data-v-fainput]{
        float:left;
        padding:5px;
    }

    p.formLabel[data-v-fainput]{
        margin-top:10px;
        margin-bottom: 5px;
    }

    input.commentInput[data-v-fainput]{
        width:250px;
    }

    table[data-v-fainput]{
        margin: 10px 0px;
    }
</style>





